  .container {
    min-width: 1200px;
    padding-left: 15px;
    padding-right: 16px;
  }
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.button-big {
  cursor: pointer;
  border: 0;
  border-radius: 22px;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  display: inline-block;
}

.button-normal {
  cursor: pointer;
  border: 0;
  border-radius: 20px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
  display: inline-block;
}

.button-micro {
  cursor: pointer;
  border: 0;
  border-radius: 20px;
  font-family: DM Sans;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.25;
  display: inline-block;
}

